.text-center {
	text-align: center; }

.color-red {
	color: $red; }

.md-show {
	display: none;
	@include md {
		display: block; } }
.md-hide {
	@include md {
		display: none !important; } }

.sm-show {
	display: none !important;
	@include sm {
		display: block !important; } }
.sm-hide {
	@include sm {
		display: none !important; } }

