html {
	&.is-open-menu {
		overflow-y: scroll; } }

body {
	@include rob;
	@include f(16px,400,2.375);
	color: $black;
	min-width: 320px;
	@include font-smoothing;
	opacity: 0;
	visibility: hidden;
	transition: opacity .35s ease-in-out, visibility .35s ease-in-out;
	@include md {
		@include f(14px,400,2.43); }
	&.is-loaded {
		opacity: 1;
		visibility: visible; }
	.is-open-menu & {
		position: fixed;
		height: 100vh;
		width: 100%;
		overflow: hidden; } }

// TItLES
.title {
	@include hss;
	@include f(14px,700,1.57);
	text-transform: uppercase; }
.title-lg {
	@include hss;
	@include f(36px,400,1.56);
	@include sm {
		@include f(24px,400,1.8);
		letter-spacing: 0.005em; } }
.title-mid {
	@include hss;
	@include f(28px,400,1.64);
	@include sm {
		@include f(24px,400,2.23); } }
.title-md {
	@include hss;
	@include f(22px,400,2);
	@include sm {
		@include f(18px,400,2.23); } }

.readmore {
	position: relative;
	padding-left: 90px;
	color: $red;
	@include f(16px,400,2.375);
	transition: color .3s;
	vertical-align: top;
	display: inline-block;
	@include hover {
		color: darken($red, 10%);
		&:before {
			transform: scaleX(0.5); } }
	@include sm {
		padding-left: 0;
		@include f(14px, 400, 1.84); }
	&:before {
		position: absolute;
		content: '';
		top: 50%;
		left: 0;
		width: 70px;
		height: 1px;
		transform-origin: right;
		transition: transform .3s;
		background-color: $red;
		@include sm {
			display: none; } } }
