.footer {
	padding: 93px 0 97px;
	background-image: url(../img/footer-bg.svg);
	background-position: 68% 214px;
	background-repeat: no-repeat;
	@include sm {
		padding: 72px 0;
		text-align: center;
		background: none; }
	a {
		color: $black;
		transition: opacity .3s;
		@include hover {
			opacity: .6; } }
	&__col-left,
	&__col-right {
		display: flex;
		flex-direction: column;
		justify-content: space-between; }
	&__col-left {
		@include fs(57.7%);
		@include sm {
			@include fs(100%); } }
	&__col-right {
		@include fs(42.3%);
		@include sm {
			margin-top: 40px;
			@include fs(100%); } }
	&__logo {
		margin-top: 3px;
		margin-bottom: 28px;
		vertical-align: top;
		display: inline-block;
		img {
			max-width: 100%;
			display: block; } }
	&__title-mid {
		max-width: 182px;
		@include sm {
			margin: 0 auto; } }
	&__title {
		&:not(:first-child) {
			margin-top: 39px;
			@include sm {
				margin-top: 25px; } }
		&:not(:last-child) {
			margin-bottom: 18px;
			@include sm {
				margin-bottom: 11px; } } }
	&__link {
		a {
			opacity: 0.6;
			@include hover {
				opacity: 1; } } }
	&__nav {
		margin-bottom: -5px;
		flex-wrap: wrap;
		line-height: 2;
		display: flex;
		@include sm {
			margin-bottom: 7px;
			justify-content: center; }
		li {
			margin-bottom: 5px;
			&:not(:last-child) {
				margin-right: 19px; } } }
	&__text {
		opacity: 0.3; }
	&__dev {
		margin-top: 33px;
		@include sm {
			margin-top: 15px; }
		.footer__text {
			display: inline; } }
	&__soc {
		margin-top: 42px; } }
.soc {
	margin-bottom: -5px;
	flex-wrap: wrap;
	line-height: 2;
	display: flex;
	@include sm {
		justify-content: center; }
	li {
		margin-bottom: 5px;
		&:not(:last-child) {
			margin-right: 40px; } }
	a {
		font-size: 18px;
		color: $black;
		transition: opacity .3s;
		@include hover {
			opacity: .6; } } }
