.header {
	position: relative;
	z-index: 10;
	padding-top: 40px;
	margin-bottom: 143px;
	&_abs {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		margin-bottom: 0; }
	@include lg {
		padding-top: 28px; }
	&__inner {
		padding: 0 70px;
		margin: 0 auto;
		max-width: 1920px;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		display: flex;
		@include md {
			padding: 0 30px; } }
	&__logo {
		position: relative;
		z-index: 2; }
	&__nav {
		padding: 0 20px;
		@include md {
			position: absolute;
			top: 0;
			left: 0;
			width: 100vw;
			height: 100vh;
			padding: 50px 30px;
			opacity: 0;
			visibility: hidden;
			transition: opacity .3s, visibility .3s;
			background: rgba($black, .8);
			overflow: auto;
			align-items: center;
			justify-content: center;
			display: flex; }
		.is-open-menu & {
			@include md {
				opacity: 1;
				visibility: visible; } } }
	&__login {
		flex-wrap: wrap;
		justify-content: center;
		align-items: center; }
	&__link {
		margin-right: 33px;
		@include hss;
		color: $black;
		transition: color .3s;
		@include f(18px,700,50px);
		@include hover {
			color: $red; }
		.header_abs & {
			color: $white; } } }
.logo {
	img {
		display: block; }
	&__text {
		display: none; } }
.nav {
	@include hss;
	@include f(18px,400,2.22);
	@include md {
		@include f(20px, 400, 2); }
	&__list {
		align-items: center;
		flex-wrap: wrap;
		display: flex;
		@include md {
			flex-direction: column; } }
	&__item {
		&:not(:first-child) {
			margin-left: 53px;
			@include md {
				margin: 22px 0 0; } } }
	&__link {
		position: relative;
		color: $black;
		opacity: 0.5;
		transition: opacity .3s;
		padding-bottom: 7px;
		display: block;
		@include hover {
			opacity: 1; }
		@include md {
			opacity: 1; }
		&.is-active {
			opacity: 1;
			&:before {
				position: absolute;
				content: '';
				left: 0;
				bottom: 0;
				width: 100%;
				height: 2px;
				background-color: $red; } }
		.header_abs & {
			color: $white; } } }

.btn-menu {
	position: relative;
	z-index: 2;
	width: 17px;
	height: 16px;
	background-color: transparent;
	display: none;
	@include md {
		vertical-align: top;
		display: inline-block; }
	&:before {
		position: absolute;
		content: '';
		top: -5px;
		right: -5px;
		bottom: -5px;
		left: -5px; }
	&__line {
		position: absolute;
		width: 17px;
		left: 0;
		border-bottom: 2px solid $white;
		border-radius: 2px;
		transition: transform .3s, opacity .3s;
		backface-visibility: hidden;
		will-change: transform;
		&:first-child {
			top: 0;
			transform-origin: top left;
			.is-open-menu & {
				transform: rotate(45deg) translate(1px, 1px); } }
		&:nth-child(2) {
			right: 0;
			left: auto;
			top: 7px;
			width: 12px;
			transform-origin: left;
			.is-open-menu & {
				opacity: 0;
				transform: scaleX(0); } }
		&:nth-child(3) {
			bottom: 0;
			transform-origin: bottom left;
			.is-open-menu & {
				transform: rotate(-45deg) translate(1px, -1px); } } } }
