[data-tabs] [data-tabs-container].is-open { position: relative; opacity: 1; visibility: visible; }

[data-tabs] .slick-slide [data-tabs-container] { position: relative; visibility: visible; opacity: 1; }

.tabs { padding-top: 7px; }

