.tabs .slick-dots button { position: relative; height: 16px; width: 16px; border-radius: 50%; font-size: 0; border: 2px solid #D65151; color: #D65151; background-color: #fff; }

.tabs .slick-dots button:after { position: absolute; content: ''; top: 50%; left: 50%; margin: -3px 0 0 -3px; height: 6px; width: 6px; border-radius: 50%; transform: scale(0); transition: transform .3s; background-color: currentColor; }

