.tabs__list { padding: 0 31px; justify-content: space-around; display: flex; }

.tabs__item { position: relative; padding: 7px 37px 13px; font-family: "Harmonia Sans Std", "Open Sans", sans-serif; font-size: 16px; font-weight: 400; line-height: 48px; letter-spacing: .02em; text-align: center; user-select: none; transition: color .3s, background-color .3s, opacity .3s; border-radius: 15px 15px 0 0; }

