.tabs__item[data-tabs-control="tools"]:before, .tabs__item[data-tabs-control="tools"]:after { color: #AB6DA5; }

.tabs__item[data-tabs-control="custodianship"]:before, .tabs__item[data-tabs-control="custodianship"]:after { color: #826CE7; }

.tabs__item[data-tabs-control="electronic"]:before, .tabs__item[data-tabs-control="electronic"]:after { color: #7D89E9; }

