.tabs__item.is-active[data-tabs-control="electronic"] { color: #7D89E9; background-color: rgba(125, 137, 233, 0.07); }

.tabs__item.is-active[data-tabs-control="investement"] { color: #78A7EB; background-color: rgba(120, 167, 235, 0.07); }

.tabs__item span { line-height: 1.5; transition: opacity .3s; vertical-align: middle; display: inline-block; }

.tabs__container { position: relative; }

.tabs__container:before { position: absolute; content: ''; top: 0; left: 0; right: 0; bottom: 0; opacity: 0.07; pointer-events: none; background-image: linear-gradient(180deg, #D65151 0%, #F9F9F9 100%); }

