.tabs__container[data-tabs-container="electronic"]:before { background-image: linear-gradient(180deg, #7D89E9 0%, #F9F9F9 100%); }

.tabs__container[data-tabs-container="investement"]:before { background-image: linear-gradient(180deg, #78A7EB 0%, #F9F9F9 100%); }

